<template>
  <div class="menu-setting">
    <div v-if="$route.name == '配置菜单'">
      <Button class="create" type="primary" to="/manage/gonghao/menu/create">配置新菜单栏</Button>
      <Table class="menu-table" stripe :columns="menuHeader" :data="menuData"></Table>
    </div>
    <router-view/>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        menuList: [],
        data_info: {},
        menuData: [],
        menuHeader: [
          {
            title: "菜单类型",
            align: "center",
            render(h, params) {
              let text = params.row.individual ? "个性化菜单" : "通用菜单";
              return h('span', {
                props: {}
              }, text)
            }
          },
          {
            title: "菜单性别",
            align: "center",
            render(h, params) {
              let text = params.row.individual ? (params.row.sex === '0'
                ? "未知"
                : (params.row.sex === '1'
                  ? '男'
                  : '女')) : '——';
              return h('span', {
                props: {}
              }, text)
            }
          },
          {
            key: "title",
            title: "标题",
            align: "center"
          },
          {
            title: "Action",
            align: "center",
            render: (h, params) => {
              let that = this;
              return h("div", [
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.updateOne(params.row);
                      }
                    }
                  },
                  "编辑"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.deleteOne(params.row);
                      }
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ]
      };
    },
    mounted() {
      this.showMenuList();
    },
    methods: {
      tap(item) {
        this.dialogVisible = true;
        this.data_info = item;
      },
      showMenuList() {
        this.$axios.get("/menu").then(res => {
          this.menuData = res.data.data.reverse();
        });
      },
      updateOne(row) {
        this.$router.push({
          name: "修改菜单",
          params: row
        });
      },
      deleteOne(row) {
        this.$Modal.confirm({
          title: "提示:",
          content: "确认删除这条内容吗？",
          onOk: () => {
            this.$axios.get("/menu/del", {params: {id: row._id}})
              .then(res => {
                this.showMenuList();
                this.$Message.info(res.data.success);
              });
          }
        });
      }
    },
    watch: {
      $route(to, from) {
        if (from.name == '新建菜单' || from.name == '修改菜单') {
          this.showMenuList()
        }
      }
    }
  };
</script>

<style scoped>
  .create {
    margin-bottom: 30px;
    float: left;
  }

  .menu-table {
    clear: both;
  }

  .btn-item {
    margin-right: 10px;
  }
</style>
